import * as React from 'react'
import * as SelectPrimitive from '@radix-ui/react-select'

const cn = (...classes) => classes.filter(Boolean).join(' ')

const Select = SelectPrimitive.Root

const SelectTrigger = React.forwardRef(({ className, children, ...props }, ref) => (
  <SelectPrimitive.Trigger
    ref={ref}
    className={cn(
      'flex h-10 w-full items-center justify-between rounded-md border border-[hsl(var(--border-300)/0.35)] bg-[hsl(var(--bg-100))] px-3 py-2 text-sm text-[hsl(var(--text-200))] shadow-sm hover:border-[hsl(var(--border-200)/0.5)] focus:outline-none focus:ring-2 focus:ring-[hsl(var(--text-400)/0.3)]',
      className
    )}
    {...props}
  >
    <SelectPrimitive.Value />
    <SelectPrimitive.Icon />
  </SelectPrimitive.Trigger>
))
SelectTrigger.displayName = 'SelectTrigger'

const SelectContent = React.forwardRef(({ className, children, ...props }, ref) => (
  <SelectPrimitive.Portal>
    <SelectPrimitive.Content
      ref={ref}
      className={cn('z-50 min-w-[8rem] overflow-hidden rounded-md border border-[hsl(var(--border-300)/0.35)] bg-[hsl(var(--bg-100))] p-1 text-[hsl(var(--text-200))] shadow-md', className)}
      {...props}
    >
      <SelectPrimitive.Viewport>{children}</SelectPrimitive.Viewport>
    </SelectPrimitive.Content>
  </SelectPrimitive.Portal>
))
SelectContent.displayName = 'SelectContent'

const SelectItem = React.forwardRef(({ className, children, ...props }, ref) => (
  <SelectPrimitive.Item
    ref={ref}
    className={cn(
      'relative flex w-full cursor-default select-none items-center rounded-sm px-2 py-2 text-sm outline-none hover:bg-[hsl(var(--bg-200))] text-[hsl(var(--text-200))]',
      className
    )}
    {...props}
  >
    <SelectPrimitive.ItemText>{children}</SelectPrimitive.ItemText>
  </SelectPrimitive.Item>
))
SelectItem.displayName = 'SelectItem'

const SelectValue = SelectPrimitive.Value

export { Select, SelectTrigger, SelectContent, SelectItem, SelectValue }