import * as React from "react"
import { cn } from "../../lib/utils"

const Checkbox = React.forwardRef(({ className, ...props }, ref) => {
  return (
    <input
      type="checkbox"
      className={cn(
        "h-4 w-4 rounded border-[hsl(var(--border-300)/0.3)] bg-[hsl(var(--bg-200))] text-[hsl(var(--accent-main-100))] focus:ring-2 focus:ring-[hsl(var(--accent-main-100)/0.3)]",
        className
      )}
      ref={ref}
      {...props}
    />
  )
})
Checkbox.displayName = "Checkbox"

export { Checkbox }

