import * as React from "react"
import * as TabsPrimitive from "@radix-ui/react-tabs"
import { cn } from "../../lib/utils"

const Tabs = TabsPrimitive.Root

const TabsList = React.forwardRef(({ className, ...props }, ref) => (
  <TabsPrimitive.List
    ref={ref}
    className={cn(
      "flex flex-wrap text-center text-sm font-medium border-b border-[hsl(var(--text-500)/0.2)]",
      className
    )}
    {...props}
  />
))
TabsList.displayName = TabsPrimitive.List.displayName

const TabsTrigger = React.forwardRef(({ className, ...props }, ref) => (
  <TabsPrimitive.Trigger
    ref={ref}
    className={cn(
      "flex-1 inline-block w-full rounded-t-lg transition-colors px-1 h-[3rem] text-base tracking-wider text-[hsl(var(--text-500))] border-b-2 border-[hsl(var(--text-100)/0)] hover:text-[hsl(var(--text-200))] data-[state=active]:font-bold data-[state=active]:text-[hsl(var(--text-000))] data-[state=active]:border-b-2 data-[state=active]:border-[hsl(var(--text-100))]",
      className
    )}
    {...props}
  />
))
TabsTrigger.displayName = TabsPrimitive.Trigger.displayName

const TabsContent = React.forwardRef(({ className, ...props }, ref) => (
  <TabsPrimitive.Content
    ref={ref}
    className={cn(
      "tab-content p-4",
      className
    )}
    {...props}
  />
))
TabsContent.displayName = TabsPrimitive.Content.displayName

export { Tabs, TabsList, TabsTrigger, TabsContent }

