import { useState, useEffect, useCallback } from 'react';
import GraphVisualization from './components/GraphVisualization';
import ControlsPanel from './components/ControlsPanel';
import LoadingScreen from './components/LoadingScreen';
import InfoButton from './components/InfoButton';
import ColorPickerModal from './components/ColorPickerModal';
import GroupSearchModal from './components/GroupSearchModal';
import ContextMenu from './components/ContextMenu';
import './App.css';
import { Toaster, toast } from 'sonner'
import { csvParse } from 'd3-dsv'

const DATA_URL = `${process.env.PUBLIC_URL}/graph_data.json`;
const DEFAULT_FONT = 'modern_dense';

function App() {
  const [loading, setLoading] = useState(true);
  const [loadingText, setLoadingText] = useState('Lade Daten...');
  const [isSimulating, setIsSimulating] = useState(false);
  const [simulationProgress, setSimulationProgress] = useState(0);
  const [allNodes, setAllNodes] = useState([]);
  const [allLinks, setAllLinks] = useState([]);
  const [nodes, setNodes] = useState([]);
  const [links, setLinks] = useState([]);
  const [selectedNode, setSelectedNode] = useState(null);
  const [groups, setGroups] = useState([]);
  const [nodeGroups, setNodeGroups] = useState(new Map());
  const [selectedColor, setSelectedColor] = useState('#7f6df2');
  const [searchResults, setSearchResults] = useState([]);
  const [colorModalOpen, setColorModalOpen] = useState(false);
  const [groupSearchModalOpen, setGroupSearchModalOpen] = useState(false);
  const [currentGroupForSearch, setCurrentGroupForSearch] = useState(null);
  const [contextMenu, setContextMenu] = useState(null);
  const [amendmentsUrlMap, setAmendmentsUrlMap] = useState(new Map());
  const [editColorModalOpen, setEditColorModalOpen] = useState(false);
  const [editGroupId, setEditGroupId] = useState(null);
  const [editGroupColor, setEditGroupColor] = useState('#7f6df2');

  const [settings, setSettings] = useState({
    showLabels: true,
    showLinks: false,
    nodeType: 'all',
    nodeSize: 1,
    showGroupCentroids: false,
  });
  const [isMobileControlsOpen, setIsMobileControlsOpen] = useState(false);

  // Load data
  const loadData = useCallback(async () => {
    try {
      setLoadingText('Lade data.json...');
      const response = await fetch(DATA_URL);
      if (!response.ok) {
        throw new Error(`HTTP Error: ${response.status}`);
      }

      const jsonData = await response.json();
      if (!jsonData.support_map) {
        throw new Error('Ungltiges Datenformat: support_map fehlt');
      }

      setLoadingText('Verarbeite Daten...');
      processData(jsonData.support_map);
    } catch (error) {
      console.error('Fehler beim Laden der Daten:', error);
      setLoadingText(`Fehler: ${error.message}`);
      toast.error('Fehler beim Laden der Daten: ' + error.message)
    }
  }, []);

  useEffect(() => {
    loadData();
  }, [loadData]);

  useEffect(() => {
    // Load amendments URLs from CSV
    const URL_CSV = `${process.env.PUBLIC_URL}/amendments_url.csv`;
    fetch(URL_CSV)
      .then((res) => res.text())
      .then((text) => {
        const rows = csvParse(text);
        const map = new Map();
        rows.forEach((row) => {
          const title = row.title?.trim();
          const url = row.url?.trim();
          if (title && url) {
            map.set(title, url);
          }
        });
        setAmendmentsUrlMap(map);
      })
      .catch((err) => {
        console.error('Fehler beim Laden der amendments_url.csv:', err);
      });
  }, []);

  const processData = (supportMap) => {
    const nodeMap = new Map();
    const linkList = [];

    Object.entries(supportMap).forEach(([aea, supporters]) => {
      if (!nodeMap.has(aea)) {
        nodeMap.set(aea, {
          id: aea,
          label: aea,
          type: 'antrag',
          color: getComputedStyle(document.documentElement)
            .getPropertyValue('--node-antrag')
            .trim(),
          linkCount: 0,
          font: DEFAULT_FONT,
        });
      }

      supporters.forEach((supporter) => {
        const supporterId = `${supporter[0]} | ${supporter[1]}`;

        if (!nodeMap.has(supporterId)) {
          nodeMap.set(supporterId, {
            id: supporterId,
            label: supporter[0],
            sublabel: supporter[1],
            type: 'supporter',
            color: getComputedStyle(document.documentElement)
              .getPropertyValue('--node-supporter')
              .trim(),
            linkCount: 0,
            font: DEFAULT_FONT,
          });
        }

        linkList.push({
          source: aea,
          target: supporterId,
        });

        nodeMap.get(aea).linkCount++;
        nodeMap.get(supporterId).linkCount++;
      });
    });

    const processedNodes = Array.from(nodeMap.values());
    // Convert link IDs to node references for D3
    const processedLinks = linkList.map((link) => {
      const sourceNode = typeof link.source === 'string' ? nodeMap.get(link.source) : link.source;
      const targetNode = typeof link.target === 'string' ? nodeMap.get(link.target) : link.target;
      return {
        source: sourceNode,
        target: targetNode,
      };
    });

    setAllNodes(processedNodes);
    setAllLinks(processedLinks);
    setNodes(processedNodes);
    setLinks(processedLinks);
    setLoading(false);
    setIsSimulating(true);
    setLoadingText('Simulation luft...');
  };

  // Filter nodes based on settings
  useEffect(() => {
    if (settings.nodeType === 'all') {
      setNodes([...allNodes]);
      setLinks([...allLinks]);
    } else {
      const filteredNodes = allNodes.filter((n) => n.type === settings.nodeType);
      const nodeIds = new Set(filteredNodes.map((n) => n.id));
      const filteredLinks = allLinks.filter((l) => {
        const sourceId = typeof l.source === 'object' ? l.source.id : l.source;
        const targetId = typeof l.target === 'object' ? l.target.id : l.target;
        return nodeIds.has(sourceId) && nodeIds.has(targetId);
      });
      setNodes(filteredNodes);
      setLinks(filteredLinks);
    }
  }, [settings.nodeType, allNodes, allLinks]);

  const handleNodeClick = useCallback((node) => {
    if (selectedNode === node) {
      setSelectedNode(null);
    } else {
      setSelectedNode(node);
    }
  }, [selectedNode]);

  const handleNodeRightClick = useCallback((event, node) => {
    event.preventDefault();
    setContextMenu({
      x: event.clientX,
      y: event.clientY,
      node,
    });
  }, []);

  const handleSearch = useCallback((query) => {
  if (!query) {
  setSearchResults([]);
  return;
  }
  
  const matches = allNodes
  .filter(
  (node) =>
  node.label.toLowerCase().includes(query.toLowerCase()) ||
  (node.sublabel && node.sublabel.toLowerCase().includes(query.toLowerCase()))
  )
  .slice(0, 10);
  
  setSearchResults(matches);
  }, [allNodes]);

  const handleHighlightNode = useCallback((nodeId) => {
    const node = allNodes.find((n) => n.id === nodeId);
    if (node) {
      setSelectedNode(node);
       setSearchResults([]);
       // Zoom to the selected node
      if (window.zoomToNode) {
        window.zoomToNode(node);
      }
    }
  }, [allNodes]);

  const handleCreateGroup = useCallback((name, color) => {
    const group = {
      id: Date.now(),
      name,
      color,
      nodes: [],
    };
    setGroups((prev) => [...prev, group]);
  }, []);

  const handleDeleteGroup = useCallback((groupId) => {
    setGroups((prev) => prev.filter((g) => g.id !== groupId));
    setNodeGroups((prev) => {
      const newMap = new Map(prev);
      prev.forEach((gId, nodeId) => {
        if (gId === groupId) {
          newMap.delete(nodeId);
        }
      });
      return newMap;
    });
  }, []);

  const handleOpenEditGroupColor = useCallback((groupId) => {
    const group = groups.find((g) => g.id === groupId);
    if (!group) return;
    setEditGroupId(groupId);
    setEditGroupColor(group.color || '#7f6df2');
    setEditColorModalOpen(true);
  }, [groups]);

  const handleConfirmEditGroupColor = useCallback((color) => {
    setGroups((prev) => prev.map((g) => (g.id === editGroupId ? { ...g, color } : g)));
    setEditColorModalOpen(false);
    setEditGroupId(null);
  }, [editGroupId]);

  const handleAddNodeToGroup = useCallback((nodeId, groupId) => {
    setNodeGroups((prev) => new Map(prev).set(nodeId, groupId));
  }, []);

  const handleRemoveNodeFromGroup = useCallback((nodeId) => {
    setNodeGroups((prev) => {
      const newMap = new Map(prev);
      newMap.delete(nodeId);
      return newMap;
    });
  }, []);

  const handleShowGroupSearch = useCallback((groupId) => {
    setCurrentGroupForSearch(groupId);
    setGroupSearchModalOpen(true);
  }, []);

  const handleToggleNodeInGroup = useCallback((nodeId) => {
    if (nodeGroups.get(nodeId) === currentGroupForSearch) {
      handleRemoveNodeFromGroup(nodeId);
    } else {
      handleAddNodeToGroup(nodeId, currentGroupForSearch);
    }
  }, [currentGroupForSearch, nodeGroups, handleAddNodeToGroup, handleRemoveNodeFromGroup]);

  const handleExportGroups = useCallback(() => {
    const exportData = {
      groups,
      nodeGroups: Array.from(nodeGroups.entries()).map(([nodeId, groupId]) => ({
        nodeId,
        groupId,
      })),
    };

    const dataStr = JSON.stringify(exportData, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(dataBlob);

    const link = document.createElement('a');
    link.href = url;
    link.download = `graph-groups-${new Date().toISOString().split('T')[0]}.json`;
    link.click();

    URL.revokeObjectURL(url);
  }, [groups, nodeGroups]);

  const handleImportGroups = useCallback((event) => {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const importData = JSON.parse(e.target.result);

        if (importData.groups && importData.nodeGroups) {
          setGroups(importData.groups);
          setNodeGroups(
            new Map(importData.nodeGroups.map((item) => [item.nodeId, item.groupId]))
          );
          toast.success('Gruppen erfolgreich importiert!')
        } else {
          toast.error('Ungltiges Dateiformat')
        }
      } catch (error) {
        toast.error('Fehler beim Importieren: ' + error.message)
      }
    };
    reader.readAsText(file);

    event.target.value = '';
  }, []);

  const handleResetView = useCallback(() => {
    setSelectedNode(null);
    if (window.resetGraphView) {
      window.resetGraphView();
    }
  }, []);

  const handleSimulationProgress = useCallback((progress) => {
    setSimulationProgress(progress);
    setLoadingText(`Simulation luft... ${Math.round(progress)}%`);
  }, []);

  const handleSimulationComplete = useCallback(() => {
    setIsSimulating(false);
    setLoadingText('');
  }, []);

  const getNodeColor = useCallback(
    (node) => {
      const groupId = nodeGroups.get(node.id);
      if (groupId !== undefined) {
        const group = groups.find((g) => g.id === groupId);
        if (group) return group.color;
      }
      return node.color;
    },
    [nodeGroups, groups]
  );

  return (
    <div className="relative h-screen w-screen">
      {/* Mobile Controls Toggle Button (hidden on md and up) */}
      {!(loading || isSimulating) && (
        <button
          className="fixed top-5 right-5 z-40 md:hidden bg-[hsl(var(--primary))] hover:bg-[hsl(var(--primary)/0.9)] text-white px-4 py-3 rounded-lg shadow-lg backdrop-blur-sm border border-white/20 transition-all duration-200 flex items-center gap-2"
          onClick={() => setIsMobileControlsOpen(true)}
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
          </svg>
          Men
        </button>
      )}

      {/* Mobile Controls Overlay */}
      {!(loading || isSimulating) && isMobileControlsOpen && (
        <div className="fixed inset-0 z-50 bg-[hsl(var(--bg-100))] animate-in fade-in duration-200">
          <div className="absolute top-0 left-0 right-0 bg-[hsl(var(--bg-200))] border-b border-[hsl(var(--border))] px-4 py-3 flex items-center justify-between">
            <h2 className="text-lg font-semibold text-[hsl(var(--text-primary))]">Einstellungen</h2>
            <button
              className="bg-[hsl(var(--bg-300))] hover:bg-[hsl(var(--bg-400))] text-[hsl(var(--text-primary))] px-3 py-2 rounded-lg transition-colors duration-200 flex items-center gap-2"
              onClick={() => setIsMobileControlsOpen(false)}
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
              Schlieen
            </button>
          </div>
          <div className="absolute inset-0 top-[60px] overflow-y-auto p-4 pb-20">
            <ControlsPanel
              settings={settings}
              onSettingsChange={setSettings}
              onSearch={handleSearch}
              searchResults={searchResults}
              onHighlightNode={handleHighlightNode}
              groups={groups}
              nodeGroups={nodeGroups}
              onCreateGroup={handleCreateGroup}
              onDeleteGroup={handleDeleteGroup}
              onShowGroupSearch={handleShowGroupSearch}
              onExportGroups={handleExportGroups}
              onImportGroups={handleImportGroups}
              selectedColor={selectedColor}
              onColorSelect={() => setColorModalOpen(true)}
              onResetView={handleResetView}
              onEditGroupColor={handleOpenEditGroupColor}
            />
          </div>
        </div>
      )}

      <GraphVisualization
        nodes={nodes}
        links={links}
        selectedNode={selectedNode}
        onNodeClick={handleNodeClick}
        onNodeRightClick={handleNodeRightClick}
        settings={settings}
        nodeGroups={nodeGroups}
        groups={groups}
        getNodeColor={getNodeColor}
        onSimulationProgress={handleSimulationProgress}
        onSimulationComplete={handleSimulationComplete}
      />

      {!(loading || isSimulating) && (
        <ControlsPanel
          settings={settings}
          onSettingsChange={setSettings}
          onSearch={handleSearch}
          searchResults={searchResults}
          onHighlightNode={handleHighlightNode}
          groups={groups}
          nodeGroups={nodeGroups}
          onCreateGroup={handleCreateGroup}
          onDeleteGroup={handleDeleteGroup}
          onShowGroupSearch={handleShowGroupSearch}
          onExportGroups={handleExportGroups}
          onImportGroups={handleImportGroups}
          selectedColor={selectedColor}
          onColorSelect={() => setColorModalOpen(true)}
          onResetView={handleResetView}
          onEditGroupColor={handleOpenEditGroupColor}
        />
      )}

      <LoadingScreen loading={loading || isSimulating} loadingText={loadingText} progress={simulationProgress} />

      <InfoButton
        nodeCount={nodes.length}
        linkCount={links.length}
        selectedNode={selectedNode}
      />

      <ColorPickerModal
        open={colorModalOpen}
        onOpenChange={setColorModalOpen}
        selectedColor={selectedColor}
        onColorConfirm={setSelectedColor}
      />

      <GroupSearchModal
        open={groupSearchModalOpen}
        onOpenChange={setGroupSearchModalOpen}
        allNodes={allNodes}
        currentGroupId={currentGroupForSearch}
        nodeGroups={nodeGroups}
        onToggleNode={handleToggleNodeInGroup}
      />

      {contextMenu && (
        <ContextMenu
          x={contextMenu.x}
          y={contextMenu.y}
          node={contextMenu.node}
          groups={groups}
          nodeGroups={nodeGroups}
          onAddToGroup={handleAddNodeToGroup}
          onRemoveFromGroup={handleRemoveNodeFromGroup}
          onClose={() => setContextMenu(null)}
          amendmentsUrlMap={amendmentsUrlMap}
        />
      )}
    <Toaster richColors position="top-right" />
    </div>
  );
}

export default App;
