import { useState } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from './ui/dialog';
import { Button } from './ui/button';
import { Input } from './ui/input';

const COLOR_PRESETS = ['#e05252', '#52e069', '#7f6df2', '#f59e0b', '#3b82f6'];

export default function ColorPickerModal({ open, onOpenChange, selectedColor, onColorConfirm }) {
  const [color, setColor] = useState(selectedColor);
  const [customColor, setCustomColor] = useState('');

  const handlePresetClick = (presetColor) => {
    setColor(presetColor);
    setCustomColor('');
  };

  const handleCustomColorChange = (value) => {
    setCustomColor(value);
    if (/^#[0-9A-F]{6}$/i.test(value)) {
      setColor(value);
    }
  };

  const handleConfirm = () => {
    onColorConfirm(color);
    onOpenChange(false);
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle className="text-[hsl(var(--text-000))]">Farbe whlen</DialogTitle>
        </DialogHeader>
        <div className="p-4 md:p-5">
          <div className="mb-4">
            <label className="mb-3 block text-sm font-medium text-[hsl(var(--text-200))]">
              Voreinstellungen
            </label>
            <div className="flex flex-wrap gap-3">
              {COLOR_PRESETS.map((preset) => (
                <div
                  key={preset}
                  className={`color-preset ${color === preset ? 'selected' : ''}`}
                  style={{ background: preset }}
                  onClick={() => handlePresetClick(preset)}
                />
              ))}
            </div>
          </div>
          <div className="mb-4">
            <label className="mb-2 block text-sm font-medium text-[hsl(var(--text-200))]">
              Eigene Farbe (HEX)
            </label>
            <Input
              type="text"
              id="custom-color-input"
              placeholder="#000000"
              value={customColor}
              onChange={(e) => handleCustomColorChange(e.target.value)}
            />
          </div>
          <Button onClick={handleConfirm} className="w-full">
            Farbe besttigen
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}

