import { useState } from 'react';
import { Tabs, TabsList, TabsTrigger, TabsContent } from './ui/tabs';
import { Button } from './ui/button';
import { Input } from './ui/input';
import { Select, SelectTrigger, SelectContent, SelectItem, SelectValue } from './ui/select';
import { Checkbox } from './ui/checkbox';
import { Slider } from './ui/slider';
import { ChevronLeft, ChevronDown } from 'lucide-react';
import { toast } from 'sonner'

export default function ControlsPanel({
  settings,
  onSettingsChange,
  onSearch,
  searchResults,
  onHighlightNode,
  groups,
  nodeGroups,
  onCreateGroup,
  onDeleteGroup,
  onShowGroupSearch,
  onExportGroups,
  onImportGroups,
  selectedColor,
  onColorSelect,
  onResetView,
  onEditGroupColor,
}) {
  const [groupName, setGroupName] = useState('');

  const getNodeCountForGroup = (groupId) => {
    let count = 0;
    nodeGroups.forEach((gId) => {
      if (gId === groupId) count++;
    });
    return count;
  };

  return (
    <div className="fixed left-5 top-5 z-30 w-[320px] rounded-xl bg-[hsl(var(--bg-100))] p-4 shadow-[0_0.25rem_1.25rem_hsl(var(--always-black)/3.5%),0_0_0_0.5px_hsla(var(--border-300)/0.15)]">
      <Tabs defaultValue="view">
        <TabsList className="w-full">
          <TabsTrigger value="view">Ansicht</TabsTrigger>
          <TabsTrigger value="search">Suche</TabsTrigger>
          <TabsTrigger value="groups">Gruppen</TabsTrigger>
        </TabsList>

        {/* View Tab */}
        <TabsContent value="view" className="tab-content p-4">
          <h3 className="mb-2 text-[hsl(var(--text-200))] flex items-center text-center min-w-0 font-serif text-2xl">
            Darstellung
          </h3>
          <hr className="mb-2 mt-2 border-[hsl(var(--text-500)/0.2)]" />

          <div className="mb-2 mt-2">
            <label className="mb-2 block text-sm font-medium text-[hsl(var(--text-000))]">
              Node Typ
            </label>
            <Select value={settings.nodeType} onValueChange={(value) => onSettingsChange({ ...settings, nodeType: value })}>
              <SelectTrigger>
                <SelectValue placeholder="Node Typ auswhlen" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Alle</SelectItem>
                <SelectItem value="antrag">Antrge</SelectItem>
                <SelectItem value="supporter">Supporter</SelectItem>
              </SelectContent>
            </Select>
          </div>

          <div className="mb-2 mt-2">
            <label className="mb-2 block text-sm font-medium text-[hsl(var(--text-000))]">
              Node Gre
            </label>
            <Slider
              value={[settings.nodeSize]}
              onValueChange={(value) => onSettingsChange({ ...settings, nodeSize: value[0] })}
              min={0.5}
              max={2}
              step={0.1}
            />
          </div>

          <div className="mb-2 mt-2">
            <Checkbox
              checked={settings.showLabels}
              onCheckedChange={(checked) => onSettingsChange({ ...settings, showLabels: !!checked })}
            />
            <span className="ml-2 text-[hsl(var(--text-200))]">Labels anzeigen</span>
          </div>

          <div className="mb-2 mt-2">
            <Checkbox
              checked={settings.showLinks}
              onCheckedChange={(checked) => onSettingsChange({ ...settings, showLinks: !!checked })}
            />
            <span className="ml-2 text-[hsl(var(--text-200))]">Verbindungen anzeigen</span>
          </div>

          <div className="mb-2 mt-2">
            <Checkbox
              checked={settings.showGroupCentroids}
              onCheckedChange={(checked) => onSettingsChange({ ...settings, showGroupCentroids: !!checked })}
            />
            <span className="ml-2 text-[hsl(var(--text-200))]">Gruppen-Mittelpunkte anzeigen</span>
          </div>

          <div className="mt-4">
            <Button variant="outline" onClick={onResetView}>
              Ansicht zurcksetzen
            </Button>
          </div>
        </TabsContent>

        {/* Search Tab */}
        <TabsContent value="search" className="tab-content p-4">
          <h3 className="mb-2 text-[hsl(var(--text-200))] flex items-center text-center min-w-0 font-serif text-2xl">
            Suche
          </h3>
          <hr className="mb-2 mt-2 border-[hsl(var(--text-500)/0.2)]" />

          <div className="mb-2 mt-2">
            <label
              htmlFor="search"
              className="mb-2 block text-sm font-medium text-[hsl(var(--text-200))]"
            >
              Node suchen
            </label>
            <Input
              type="text"
              id="search"
              placeholder="Name oder KV eingeben..."
              onChange={(e) => onSearch(e.target.value)}
            />
          </div>

          <div id="search-results" className="space-y-1">
            {searchResults.map((node) => (
              <div
                key={node.id}
                className="p-2 rounded cursor-pointer text-sm mb-1 bright-container"
                onClick={() => onHighlightNode(node.id)}
              >
                <span className="text-[hsl(var(--text-000))]">{node.label}</span>
                {node.sublabel && (
                  <span className="text-[hsl(var(--text-400))] text-xs ml-2">
                    ({node.sublabel})
                  </span>
                )}
              </div>
            ))}
          </div>
        </TabsContent>

        {/* Groups Tab */}
        <TabsContent value="groups" className="tab-content p-4">
          <h3 className="mb-2 text-[hsl(var(--text-200))] flex items-center text-center min-w-0 font-serif text-2xl">
            Gruppen
          </h3>
          <hr className="mb-2 mt-2 border-[hsl(var(--text-500)/0.2)]" />

          {/* Create Group */}
          <div className="mb-4 rounded-lg p-3 bright-container">
            <label className="mb-2 block text-sm font-medium text-[hsl(var(--text-200))]">
              Gruppenname
            </label>
            <Input
              type="text"
              value={groupName}
              onChange={(e) => setGroupName(e.target.value)}
              placeholder="Name eingeben"
            />

            <div className="mt-3 flex items-center gap-2">
              <span className="text-[hsl(var(--text-200))] text-sm">Farbe:</span>
              <span
                className="inline-block h-4 w-4 rounded border border-[hsl(var(--border))]"
                style={{ background: selectedColor }}
              />
              <Button size="sm" variant="outline" onClick={onColorSelect}>
                Farbe whlen
              </Button>
            </div>

            <div className="mt-3">
              <Button
                onClick={() => {
                  if (!groupName.trim()) {
                    toast.error('Bitte Gruppenname eingeben');
                    return;
                  }
                  onCreateGroup(groupName.trim(), selectedColor);
                  setGroupName('');
                }}
              >
                Gruppe erstellen
              </Button>
            </div>
          </div>

          {/* Groups List */}
          <div className="mb-4 rounded-lg p-3 bright-container">
            <label className="mb-2 block text-sm font-medium text-[hsl(var(--text-200))]">
              Bestehende Gruppen
            </label>

            <div className="space-y-2">
              {groups.length === 0 ? (
                <div className="text-sm text-muted-foreground">Keine Gruppen vorhanden</div>
              ) : (
                groups.map((group) => (
                  <div key={group.id} className="rounded-lg p-3 bright-container">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center gap-2">
                        <span
                          className="inline-block h-3 w-3 rounded"
                          style={{ background: group.color }}
                        />
                        <span className="text-[hsl(var(--text-200))] text-sm font-medium">
                          {group.name}
                        </span>
                        <span className="text-[hsl(var(--text-400))] text-xs">
                          ({getNodeCountForGroup(group.id)} Nodes)
                        </span>
                      </div>
                      <div className="flex gap-2">
                        <Button size="sm" variant="secondary" onClick={() => onEditGroupColor(group.id)}>
                          Farbe bearbeiten
                        </Button>
                        <Button size="sm" variant="outline" onClick={() => onShowGroupSearch(group.id)}>
                          Nodes suchen
                        </Button>
                        <Button size="sm" variant="destructive" onClick={() => onDeleteGroup(group.id)}>
                          Lschen
                        </Button>
                      </div>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>

          {/* Export / Import */}
          <div className="rounded-lg p-3 bright-container">
            <label className="mb-2 block text-sm font-medium text-[hsl(var(--text-200))]">
              Gruppen exportieren / importieren
            </label>
            <div className="flex gap-2">
              <Button variant="outline" onClick={onExportGroups}>
                Exportieren
              </Button>
              <label className="inline-flex items-center gap-2">
                <input
                  type="file"
                  accept=".json"
                  onChange={onImportGroups}
                  className="hidden"
                  id="groups-import"
                />
                <Button variant="outline" onClick={() => document.getElementById('groups-import').click()}>
                  Importieren
                </Button>
              </label>
            </div>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}


