import { useEffect, useRef, useState } from 'react';
import { toast } from 'sonner'

export default function ContextMenu({ x, y, node, groups, nodeGroups, onAddToGroup, onRemoveFromGroup, onClose, amendmentsUrlMap }) {
  const menuRef = useRef(null);
  const [position, setPosition] = useState({ x, y });

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        onClose();
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, [onClose]);

  // Adjust position to prevent menu from going off-screen
  useEffect(() => {
    if (!menuRef.current) return;

    const menu = menuRef.current;
    const menuRect = menu.getBoundingClientRect();
    const viewportWidth = window.innerWidth;
    const viewportHeight = window.innerHeight;

    let adjustedX = x;
    let adjustedY = y;

    // Check if menu goes off right edge
    if (x + menuRect.width > viewportWidth) {
      adjustedX = viewportWidth - menuRect.width - 10;
    }

    // Check if menu goes off bottom edge
    if (y + menuRect.height > viewportHeight) {
      adjustedY = viewportHeight - menuRect.height - 10;
    }

    // Check if menu goes off left edge
    if (adjustedX < 10) {
      adjustedX = 10;
    }

    // Check if menu goes off top edge
    if (adjustedY < 10) {
      adjustedY = 10;
    }

    setPosition({ x: adjustedX, y: adjustedY });
  }, [x, y]);

  const isInGroup = nodeGroups.has(node.id);
  const nodeGroupId = nodeGroups.get(node.id);
  const currentGroup = groups.find(g => g.id === nodeGroupId);

  const handleShowInfo = () => {
    const infoText = `${node.label}${node.sublabel ? ` (${node.sublabel})` : ''}  ${node.type}, Verbindungen: ${node.linkCount ?? 0}`
    toast.info(infoText)
    onClose()
  };

  const handleOpenAntragUrl = () => {
    const url = amendmentsUrlMap.get(node.id)
    if (url) {
      window.open(url, '_blank')
    } else {
      toast.error('Kein Antrag-URL fr diesen Knoten gefunden')
    }
    onClose()
  };

  return (
    <div
      ref={menuRef}
      className="fixed z-[100] min-w-[240px] rounded-md bg-popover text-popover-foreground shadow-md border border-border p-1 backdrop-blur-sm"
      style={{ left: `${position.x}px`, top: `${position.y}px` }}
    >
      {/* Header with node info */}
      <div className="px-2 py-1.5 text-sm font-semibold border-b border-border mb-1">
        {node.label}
        {node.sublabel && (
          <div className="text-xs text-muted-foreground font-normal">{node.sublabel}</div>
        )}
      </div>

      {/* Info option - always available */}
      <button
        className="relative flex cursor-default select-none items-center rounded-sm px-2 py-1.5 text-sm outline-none hover:bg-accent hover:text-accent-foreground w-full text-left transition-colors"
        onClick={handleShowInfo}
      >
        <span className="mr-2"></span>
        Infos zu diesem Knoten
      </button>

      {/* Antrag URL button - if available */}
      {amendmentsUrlMap?.has(node.id) && (
        <button
          className="relative flex cursor-default select-none items-center rounded-sm px-2 py-1.5 text-sm outline-none hover:bg-accent hover:text-accent-foreground w-full text-left transition-colors"
          onClick={handleOpenAntragUrl}
        >
          <span className="mr-2"></span>
          Antrag ffnen
        </button>
      )}

      <hr className="my-1 h-px bg-border" />

      {/* Group management */}
      {groups.length > 0 ? (
        <>
          {/* Add to group submenu */}
          <div className="relative">
            <button
              className="relative flex cursor-default select-none items-center rounded-sm px-2 py-1.5 text-sm outline-none hover:bg-accent hover:text-accent-foreground w-full text-left transition-colors"
            >
              <span className="mr-2"></span>
              Gruppe hinzufgen
              <span className="ml-auto"></span>
            </button>
            <div className="absolute left-full top-0 ml-1 min-w-[200px] rounded-md bg-popover text-popover-foreground shadow-md border border-border p-1 z-[101] hidden group-hover:block">
              {groups.map((group) => (
                <button
                  key={group.id}
                  className="relative flex cursor-default select-none items-center rounded-sm px-2 py-1.5 text-sm outline-none hover:bg-accent hover:text-accent-foreground w-full text-left transition-colors"
                  onClick={() => {
                    onAddToGroup(node.id, group.id);
                    onClose();
                  }}
                >
                  <span
                    className="inline-block w-3 h-3 rounded-full mr-2"
                    style={{ background: group.color }}
                  />
                  {group.name}
                </button>
              ))}
            </div>
          </div>
        </>
      ) : (
        <div className="px-2 py-1.5 text-sm text-muted-foreground">
          Keine Gruppen vorhanden
        </div>
      )}

      {/* Remove from group */}
      {isInGroup && currentGroup && (
        <>
          <hr className="my-1 h-px bg-border" />
          <button
            className="relative flex cursor-default select-none items-center rounded-sm px-2 py-1.5 text-sm outline-none hover:bg-destructive hover:text-destructive-foreground w-full text-left transition-colors text-destructive"
            onClick={() => {
              onRemoveFromGroup(node.id);
              onClose();
            }}
          >
            <span className="mr-2"></span>
            Aus Gruppe "{currentGroup.name}" entfernen
          </button>
        </>
      )}
    </div>
  );
}

