export default function LoadingScreen({ loading, loadingText, progress }) {
  if (!loading) return null;

  return (
    <div className="absolute inset-0 z-[100] flex items-center justify-center bg-gray-900/95 backdrop-blur-lg">
      <div className="w-96 rounded-xl bg-[hsl(var(--bg-100))] p-6 text-center shadow-[0_0.25rem_1.25rem_hsl(var(--always-black)/3.5%),0_0_0_0.5px_hsla(var(--border-300)/0.15)]">
        <h2 className="mb-3 text-lg font-semibold text-[hsl(var(--text-200))]">Simulation luft...</h2>
        <div className="h-2 w-full overflow-hidden rounded bg-[hsl(var(--bg-200))]">
          <div
            className="h-full bg-[hsl(var(--primary))] transition-all"
            style={{ width: `${progress}%` }}
          />
        </div>
        <p className="mt-3 text-sm text-[hsl(var(--text-300))]">Bitte warten, der Graph wird berechnet.</p>
      </div>
    </div>
  );
}