import { useEffect, useRef, useCallback } from 'react';
import * as d3 from 'd3';

export default function GraphVisualization({ 
  nodes = [], 
  links = [], 
  selectedNode, 
  onNodeClick, 
  onNodeRightClick,
  // hoveredNode removed
  settings,
  nodeGroups = new Map(),
  groups = [],
  getNodeColor,
  onSimulationProgress,
  onSimulationComplete
}) {
  const canvasRef = useRef(null);
  const simulationRef = useRef(null);
  const transformRef = useRef(d3.zoomIdentity);
  const animationFrameRef = useRef(null);
  const hoveredNodeRef = useRef(null);
  const lastHoveredNodeRef = useRef(null);

  // Font mapping
  const getFontFamily = useCallback((fontName) => {
    const fontMap = {
      'dyslexic': 'Dyslexic',
      'modern_dense': 'ModernDense',
      'modern_wide': 'ModernWide',
      'serif': 'Serif'
    };
    return fontMap[fontName] || fontMap['modern_dense'];
  }, []);

  // Get connected node IDs
  const getConnectedNodeIds = useCallback((nodeId) => {
    const connected = new Set();
    links.forEach(link => {
      const sourceId = typeof link.source === 'object' ? link.source.id : link.source;
      const targetId = typeof link.target === 'object' ? link.target.id : link.target;
      if (sourceId === nodeId) {
        connected.add(targetId);
      } else if (targetId === nodeId) {
        connected.add(sourceId);
      }
    });
    return connected;
  }, [links]);

  // Render function
  const render = useCallback(() => {
    const canvas = canvasRef.current;
    if (!canvas || !nodes.length) return;

    const ctx = canvas.getContext('2d');
    const width = canvas.width;
    const height = canvas.height;

    // Clear canvas
    ctx.clearRect(0, 0, width, height);

    // Apply transform
    ctx.save();
    const transform = transformRef.current;
    ctx.translate(transform.x, transform.y);
    ctx.scale(transform.k, transform.k);

    // Get connected nodes for dimming
    const connectedNodeIds = selectedNode ? getConnectedNodeIds(selectedNode.id) : new Set();
    const hoverNode = !selectedNode ? hoveredNodeRef.current : null;

    // Draw group centroids (behind links and nodes)
    if (settings.showGroupCentroids && groups && groups.length) {
      groups.forEach((group) => {
        // Collect nodes belonging to this group
        const nodesInGroup = nodes.filter((n) => nodeGroups.get(n.id) === group.id);
        if (!nodesInGroup.length) return;
        // Weighted by linkCount (minimum 1)
        let sumW = 0;
        let sumX = 0;
        let sumY = 0;
        nodesInGroup.forEach((n) => {
          const w = Math.max(1, n.linkCount || 0);
          sumW += w;
          sumX += w * (n.x || 0);
          sumY += w * (n.y || 0);
        });
        if (sumW === 0) return;
        const cx = sumX / sumW;
        const cy = sumY / sumW;
        // Draw cross
        ctx.save();
        ctx.strokeStyle = group.color || '#ffffff';
        ctx.globalAlpha = 0.5;
        ctx.lineWidth = 3;
        const size = 20; // increased cross arm length
        ctx.beginPath();
        ctx.moveTo(cx - size, cy);
        ctx.lineTo(cx + size, cy);
        ctx.moveTo(cx, cy - size);
        ctx.lineTo(cx, cy + size);
        ctx.stroke();
        ctx.restore();
      });
    }

    // Draw links
    if (settings.showLinks) {
      links.forEach(link => {
        const source = typeof link.source === 'object' ? link.source : nodes.find(n => n.id === link.source);
        const target = typeof link.target === 'object' ? link.target : nodes.find(n => n.id === link.target);
        
        if (!source || !target) return;

        const sourceId = source.id;
        const targetId = target.id;
        const isDimmedLink = !!selectedNode && sourceId !== selectedNode.id && targetId !== selectedNode.id;
        const isHoveredLink = !!hoverNode && (sourceId === hoverNode.id || targetId === hoverNode.id);
        const isConnectedToHovered = !!hoverNode && !selectedNode && (sourceId === hoverNode.id || targetId === hoverNode.id);
        const isConnectedToSelected = !!selectedNode && (sourceId === selectedNode.id || targetId === selectedNode.id);
        
        ctx.save();
        // Base styling
        let stroke = 'rgba(153, 153, 153, 0.3)';
        let width = 1;
        if (isConnectedToHovered) {
          stroke = 'rgba(255, 255, 255, 0.6)';
          width = 3;
        } else if (isHoveredLink) {
          stroke = 'rgba(255, 255, 255, 0.45)';
          width = 2;
        } else if (isConnectedToSelected) {
          // Highlight links connected to the selected node
          stroke = 'rgba(255, 255, 255, 0.7)';
          width = 3;
        } else if (isDimmedLink) {
          // Dim non-connected links when a node is selected
          stroke = 'rgba(153, 153, 153, 0.1)';
          width = 1;
        }
        ctx.strokeStyle = stroke;
        ctx.lineWidth = width;
        ctx.beginPath();
        ctx.moveTo(source.x, source.y);
        ctx.lineTo(target.x, target.y);
        ctx.stroke();
        ctx.restore();
      });
    }

    // Draw nodes
    nodes.forEach(node => {
      const radius = (5 + Math.sqrt(node.linkCount) * 2) * settings.nodeSize;
      const isSelected = !!selectedNode && node.id === selectedNode.id;
      const isDimmedNode = !!selectedNode && node.id !== selectedNode.id && !connectedNodeIds.has(node.id);
      const isHovered = !!hoverNode && node.id === hoverNode.id;

      ctx.save();
      if (isDimmedNode) {
        ctx.globalAlpha = 0.2;
      }
      // Draw node circle
      ctx.beginPath();
      ctx.arc(node.x, node.y, radius, 0, 2 * Math.PI);
      ctx.fillStyle = getNodeColor(node);
      ctx.fill();

      // Draw selection/hover border
      if (isSelected || isHovered) {
        ctx.strokeStyle = '#ffffff';
        ctx.lineWidth = isSelected ? 2 : 1.5;
        ctx.stroke();
      }

      // Hover halo when nothing is selected
      if (isHovered && !selectedNode) {
        ctx.save();
        ctx.shadowColor = '#ffffff';
        ctx.shadowBlur = 15;
        ctx.strokeStyle = 'rgba(255, 255, 255, 0.8)';
        ctx.lineWidth = 4;
        ctx.beginPath();
        ctx.arc(node.x, node.y, radius + 6, 0, 2 * Math.PI);
        ctx.stroke();
        ctx.restore();
      }

      ctx.restore();
    });

    // Draw labels with culling based on zoom level
    const currentZoom = transformRef.current.k;
    const minZoomForLabels = 0.5; // Only show labels when zoomed in enough

    const shouldShowLabel = (node) => {
      if (currentZoom < minZoomForLabels) {
        if (selectedNode && node.id === selectedNode.id) return true;
        if (hoverNode && node.id === hoverNode.id) return true;
        return false;
      }
      if (settings.showLabels) return true;
      if (selectedNode && node.id === selectedNode.id) return true;
      if (hoverNode && node.id === hoverNode.id) return true;
      if (selectedNode && connectedNodeIds.has(node.id)) return true;
      return false;
    };

    nodes.forEach(node => {
      if (!shouldShowLabel(node)) return;

      const radius = (5 + Math.sqrt(node.linkCount) * 2) * settings.nodeSize;
      const isDimmedNode = !!selectedNode && node.id !== selectedNode.id && !connectedNodeIds.has(node.id);

      ctx.save();
      if (isDimmedNode) {
        ctx.globalAlpha = 0.2;
      }
      // Set font
      const fontFamily = getFontFamily(node.font);
      ctx.font = `12px ${fontFamily}`;
      ctx.fillStyle = getComputedStyle(document.documentElement)
        .getPropertyValue('--text-primary')
        .trim() || '#faf9f5';
      ctx.textAlign = 'left';
      ctx.textBaseline = 'middle';

      // Draw main label
      const labelX = node.x + radius + 5;
      const labelY = node.y;
      ctx.fillText(node.label, labelX, labelY);

      // Draw sublabel if exists
      if (node.sublabel) {
        ctx.font = `10px ${fontFamily}`;
        ctx.fillStyle = getComputedStyle(document.documentElement)
          .getPropertyValue('--text-secondary')
          .trim() || '#faf9f5';
        ctx.fillText(node.sublabel, labelX, labelY + 12);
      }

      ctx.restore();
    });

    ctx.restore();
  }, [nodes, links, selectedNode, /* hoveredNode removed */ settings, getNodeColor, getFontFamily, getConnectedNodeIds, nodeGroups, groups]);

  // Initialize canvas - simulation runs once and stops permanently
  const hasSimulatedRef = useRef(false);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas || !nodes.length) return;

    const width = window.innerWidth;
    const height = window.innerHeight;
    canvas.width = width;
    canvas.height = height;

    // Only run simulation once
    if (!hasSimulatedRef.current) {
      hasSimulatedRef.current = true;

      // Stop existing simulation
      if (simulationRef.current) {
        simulationRef.current.stop();
      }

      // Create new simulation
      const simulation = d3.forceSimulation(nodes)
        .force("link", d3.forceLink(links).id(d => d.id).distance(100))
        .force("charge", d3.forceManyBody().strength(-300))
        .force("center", d3.forceCenter(width / 2, height / 2))
        .force("collision", d3.forceCollide().radius(30))
        .alphaDecay(0.02)
        .alphaMin(0.001);

      simulationRef.current = simulation;

      let iterations = 0;
      const maxIterations = 300;

      // Simulation tick with progress updates
      simulation.on('tick', () => {
        iterations++;
        const progress = Math.min(100, (iterations / maxIterations) * 100);
        
        if (onSimulationProgress) {
          onSimulationProgress(progress);
        }

        // Render on each tick during simulation
        if (animationFrameRef.current) {
          cancelAnimationFrame(animationFrameRef.current);
        }
        animationFrameRef.current = requestAnimationFrame(render);
      });

      // Simulation end - stop permanently
      simulation.on('end', () => {
        simulation.stop();
        // Remove all forces to prevent any movement
        simulation.force("link", null);
        simulation.force("charge", null);
        simulation.force("center", null);
        simulation.force("collision", null);
        render(); // Final render
        if (onSimulationComplete) {
          onSimulationComplete();
        }
      });
    } else {
      // Simulation already completed, just render
      render();
    }

    return () => {
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
    };
  }, [nodes, links, render, onSimulationProgress, onSimulationComplete]);

  // Re-render when settings or selection changes (simulation is stopped)
  useEffect(() => {
    render();
  }, [selectedNode, /* hoveredNode removed */ settings, nodeGroups, groups, render]);

  // Handle zoom
  const zoomRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const zoom = d3.zoom()
      .scaleExtent([0.1, 4])
      .on('zoom', (event) => {
        transformRef.current = event.transform;
        render();
      });

    // Initialize with current transform
    d3.select(canvas)
      .call(zoom)
      .call(zoom.transform, transformRef.current);
    
    zoomRef.current = zoom;

    return () => {
      d3.select(canvas).on('.zoom', null);
    };
  }, [render]);

  // Handle mouse events for node interaction
  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const getNodeAt = (x, y) => {
      const transform = transformRef.current;
      const scale = transform.k;
      const tx = transform.x;
      const ty = transform.y;

      // Transform mouse coordinates to graph coordinates
      const graphX = (x - tx) / scale;
      const graphY = (y - ty) / scale;

      // Find node at position (check from end to start for top-most)
      for (let i = nodes.length - 1; i >= 0; i--) {
        const node = nodes[i];
        const radius = (5 + Math.sqrt(node.linkCount) * 2) * settings.nodeSize;
        const dx = graphX - node.x;
        const dy = graphY - node.y;
        const distance = Math.sqrt(dx * dx + dy * dy);
        
        if (distance <= radius) {
          return node;
        }
      }
      return null;
    };

    const handleMouseMove = (event) => {
      const rect = canvas.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;

      // No dragging - just handle hover
      const node = getNodeAt(x, y);
      if (node !== lastHoveredNodeRef.current) {
        lastHoveredNodeRef.current = node;
        hoveredNodeRef.current = node;
        render();
      }
    };

    const handleMouseDown = (event) => {
      const rect = canvas.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;

      const node = getNodeAt(x, y);
      if (node) {
        if (event.button === 0) { // Left click
          // Don't allow dragging - nodes should stay fixed
          // Just select the node
        } else if (event.button === 2) { // Right click
          event.preventDefault();
          onNodeRightClick(event, node);
        }
      }
    };

    const handleMouseUp = () => {
      // No dragging, so nothing to do here
    };

    const handleClick = (event) => {
      const rect = canvas.getBoundingClientRect();
      const x = event.clientX - rect.left;
      const y = event.clientY - rect.top;

      const node = getNodeAt(x, y);
      if (node) {
        onNodeClick(node);
      } else {
        onNodeClick(null);
      }
    };

    canvas.addEventListener('mousemove', handleMouseMove);
    canvas.addEventListener('mousedown', handleMouseDown);
    canvas.addEventListener('mouseup', handleMouseUp);
    canvas.addEventListener('click', handleClick);
    canvas.addEventListener('contextmenu', (e) => e.preventDefault());

    return () => {
      canvas.removeEventListener('mousemove', handleMouseMove);
      canvas.removeEventListener('mousedown', handleMouseDown);
      canvas.removeEventListener('mouseup', handleMouseUp);
      canvas.removeEventListener('click', handleClick);
    };
  }, [nodes, settings, onNodeClick, onNodeRightClick, render]);

  // Handle window resize
  useEffect(() => {
    const handleResize = () => {
      const canvas = canvasRef.current;
      if (!canvas) return;

      const width = window.innerWidth;
      const height = window.innerHeight;
      canvas.width = width;
      canvas.height = height;

      // Don't restart simulation on resize - nodes should stay fixed
      render();
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [render]);

  // Expose reset view and zoom to node functions
  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    window.resetGraphView = () => {
      transformRef.current = d3.zoomIdentity;
      // Apply the transform to the zoom behavior
      if (zoomRef.current) {
        d3.select(canvas)
          .transition()
          .duration(750)
          .call(zoomRef.current.transform, d3.zoomIdentity);
      }
      render();
    };

    window.zoomToNode = (node) => {
      if (!node || !canvas) return;
      
      const width = canvas.width;
      const height = canvas.height;
      
      // Calculate transform to center on node
      const scale = 1.5; // Zoom level
      const transform = d3.zoomIdentity
        .translate(width / 2, height / 2)
        .scale(scale)
        .translate(-node.x, -node.y);
      
      transformRef.current = transform;
      
      // Apply the transform with animation
      if (zoomRef.current) {
        d3.select(canvas)
          .transition()
          .duration(750)
          .call(zoomRef.current.transform, transform);
      }
      
      render();
    };
  }, [render]);

  return (
    <canvas 
      ref={canvasRef}
      className="absolute inset-0 cursor-grab"
    />
  );
}
