import { useState } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from './ui/dialog';
import { Input } from './ui/input';

export default function GroupSearchModal({
  open,
  onOpenChange,
  allNodes,
  currentGroupId,
  nodeGroups,
  onToggleNode,
}) {
  const [searchQuery, setSearchQuery] = useState('');

  const matches = allNodes
    .filter(
      (node) =>
        node.label.toLowerCase().includes(searchQuery.toLowerCase()) ||
        (node.sublabel && node.sublabel.toLowerCase().includes(searchQuery.toLowerCase()))
    )
    .slice(0, 100);

  const isInGroup = (nodeId) => nodeGroups.get(nodeId) === currentGroupId;

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-2xl">
        <DialogHeader>
          <DialogTitle className="text-[hsl(var(--text-000))]">
            Nodes zu Gruppe hinzufgen
          </DialogTitle>
        </DialogHeader>
        <div className="p-4 md:p-5">
          <Input
            type="text"
            id="group-node-search"
            placeholder="Node suchen..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="mb-4"
          />
          <div id="group-search-results" className="max-h-96 space-y-1 overflow-y-auto">
            {searchQuery && matches.length === 0 && (
              <p className="text-[hsl(var(--text-400))] text-sm p-2">Keine Ergebnisse gefunden</p>
            )}
            {matches.map((node) => {
              const inGroup = isInGroup(node.id);
              return (
                <div
                  key={node.id}
                  className="p-2 rounded cursor-pointer text-sm mb-1 flex justify-between items-center bright-container"
                  onClick={() => onToggleNode(node.id)}
                >
                  <div>
                    <span className="text-[hsl(var(--text-000))]">{node.label}</span>
                    {node.sublabel && (
                      <span className="text-[hsl(var(--text-400))] text-xs ml-2">
                        ({node.sublabel})
                      </span>
                    )}
                  </div>
                  <span
                    className={`text-xs ${
                      inGroup
                        ? 'text-[hsl(var(--success-100-full))]'
                        : 'text-[hsl(var(--text-400))]'
                    }`}
                  >
                    {inGroup ? '' : '+'}
                  </span>
                </div>
              );
            })}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}

